@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");
* {
  box-sizing: border-box;
}

body {
  font-family: "Noto Sans", sans-serif;
  margin: 0;
}

h1 {
  text-align: center;
}

.form {
  display: flex;
  justify-content: center;
  gap: 24px;
}
.form input {
  width: 124px;
  height: 54px;
  border: solid 1px black;
  border-radius: 14px;
  font-size: 42px;
  text-align: center;
}
.form input.wrong {
  border-color: red;
  color: red;
}
.form input.wrong:focus-visible {
  border-color: red;
}

.result {
  margin-top: 24px;
}
.result p {
  margin: 0;
  text-align: center;
}

.btn-wrapper {
  margin-top: 24px;
  display: flex;
  justify-content: center;
}
.btn-wrapper button {
  font-size: 24px;
  cursor: pointer;
}